<template>
  <div class="single-blog">
    <div class="row">
      <div class="col-sm-6">
        <p class="small">Member Feature Story</p>
        <h1 class="blog-title">{{blog.title}}</h1>
        <p class="small">Author: {{blog.selectedAuthor}} | {{createdDate}}</p>
      </div>
      <div class="col-sm-6 post-image__col">
        <div class="post-image" v-bind:style="{ 'background-image': 'url(' + blog.imgUrl + ')' }">
          <!-- Get Background Image Here -->
        </div>
      </div>
    </div>

    <div class="row post-content__row">
      <div class="col-sm-offset-2 col-sm-8">
        <p>
          {{blog.content}}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
      createdDate: '',
    };
  },

  created() {
    axios.get(`https://happymarkup-3b57f.firebaseio.com/posts/${this.id}.json`)
      .then((response) => {
        const data = response.data;
        const objDate = data.timeStamp;
        const locale = 'en-us';
        const date = new Date(objDate).getDate();
        const month = new Date(objDate).toLocaleString(locale, { month: 'short' });
        this.createdDate = `${month} ${date}`;
        this.blog = data;
        console.log(this.blog);
      });
  },

};
</script>

