<template>

  <div class="blog-list">
    <ul class="blog-filter">
      <li :class="['blog-filter__item', { 'active' : catFilterKey === 'all' }]"
      v-uppercaseIt @click.prevent="catFilterKey = 'all'">All</li>
      <li :class="['blog-filter__item', { 'active' : catFilterKey === category }]"
      v-uppercaseIt v-for="(category, index) in categories"
      :key="index" @click.prevent="catFilterKey = category">{{category}}</li>
    </ul>
    <ul>
      <li class="blog-list-item" v-for="(blog, index) in filtered" :key="index">
        <router-link :to="'/blog/' + blog.id" class="blog-link">
        <div class="row">
          <div class="col-sm-3">
            <!-- Image for the blog -->
            <img :src="blog.imgUrl" :alt="blog.title">
          </div>
          <div class="col-sm-9">
            <!-- Content for the blog -->
            <h2 class="blog-title" v-uppercaseIt>{{blog.title}}</h2>
            <div class="blog-info">
              <span v-if="blog.selectedAuthor">Author: {{blog.selectedAuthor}} |</span>
              <span v-if="!blog.selectedAuthor">Author: Admin |</span>
              <span> Created: {{createdDate}}</span> |
              <span> Category: {{blog.selectedCategory}}</span>
            </div>
            <div class="blog-content">
              <p>{{blog.content}}</p>
            </div>
          </div>
        </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      blogList: [],
      catFilterKey: 'all',
      createdDate: '',
      categories: [],
    };
  },

  computed: {
    filtered() {
      if (this.catFilterKey === 'all') { return this.blogList; }
      return this.blogList.filter(blog => blog.selectedCategory.match(this.catFilterKey));
    },
  },

  created() {
    axios.get('https://happymarkup-3b57f.firebaseio.com/posts.json')
      .then(response => response).then((data) => {
        const d = data.data;
        Object.keys(d).forEach((key) => {
          d[key].id = key;
          const cat = d[key].selectedCategory.charAt(0).toUpperCase();
          const cate = cat + d[key].selectedCategory.slice(1);
          this.categories.push(cate);
          d[key].selectedCategory = cate;
          this.blogList.push(d[key]);
          console.log(this.blogList);
          // eslint-disable-next-line
          this.blogList.sort((a, b) => {
            return b.timeStamp - a.timeStamp;
          });
          const objDate = d[key].timeStamp;
          const locale = 'en-us';
          const date = new Date(objDate).getDate();
          const month = new Date(objDate).toLocaleString(locale, { month: 'short' });
          this.createdDate = `${month} ${date}`;
          console.log(this.createdDate);
        });
      }).catch((error) => {
        console.log(error);
      });
  },
};
</script>

